import { Trello } from "../types/trello";

let t: Trello.PowerUp.IFrame;

export function useProvidedTrello() {
    if (t === undefined) {
        t = window.TrelloPowerUp.iframe({
            appName: process.env.REACT_APP_POWERUP_NAME,
            appKey: process.env.REACT_APP_POWERUP_APP_KEY,
        });
    }

    return t;
}
