import { evaluateMathExpression } from "./mathParsing";

export function parseTimeDelta(timeDeltaString: string) {
    timeDeltaString = timeDeltaString.replaceAll(" ", "");
    timeDeltaString = timeDeltaString.replaceAll(",", ".");
    timeDeltaString = timeDeltaString.toLowerCase();

    if (!/^([^a-z]+[a-z]+)+$/.test(timeDeltaString)) {
        // console.log("desk rejected");
        return NaN;
    }

    let minutes = 0;

    for (const match of timeDeltaString.matchAll(/([^a-z]+)([a-z]+)/g)) {
        const timeLabel = match[2];

        if (timeLabel.length !== 1)
            return NaN;

        let multiplier: number;

        if (timeLabel === "m")
            multiplier = 1;
        else if (timeLabel === "h")
            multiplier = 60;
        else
            return NaN;

        const expressionResult = evaluateMathExpression(match[1]);

        if (isNaN(expressionResult))
            return NaN;

        minutes += multiplier * expressionResult;
    }

    if (minutes <= 0)
        return NaN;

    return minutes;
}

export const hoursPerDay = 8;

export type FormatMinutesOptions = {
    zero: string,
    negative: string,
    hoursPerDay: number,
}

const defaultOptions: FormatMinutesOptions = {
    zero: "none",
    negative: "?",
    hoursPerDay: 8,
};

export function formatMinutes(minutes: number) {
    return formatMinutesWithOptions(minutes );
}

export function formatMinutesWithOptions(minutes: number, options: Partial<FormatMinutesOptions> = defaultOptions) {
    if (isNaN(minutes))
        return "NaN";

    if (minutes < 0)
        return options.negative ?? defaultOptions.negative;

    if (minutes === 0)
        return options.zero ?? defaultOptions.zero;

    const hoursPerDay = options.hoursPerDay ?? defaultOptions.hoursPerDay;

    let minutesPart = Math.floor(minutes % 60);
    const flooredHours = Math.floor(minutes / 60);
    const hoursPart = flooredHours % hoursPerDay;
    const daysPart = Math.floor(flooredHours / hoursPerDay);

    let secondsPart = 0;

    // show seconds only if minutes < 1
    if (minutes < 1) {
        secondsPart = Math.round(minutes * 60);
        minutesPart = 0;
    }

    let str = "";

    if (daysPart > 0)
        str += daysPart + "d";
    if (hoursPart > 0)
        str += hoursPart + "h";
    if (minutesPart > 0)
        str += minutesPart + "m";
    if (secondsPart > 0)
        str += secondsPart + "s";

    return str;
}

export function minutesToMs(minutes: number) {
    return minutes * 60 * 1000;
}

export function hoursToMs(hours: number) {
    return minutesToMs(hours * 60);
}

export function msToMinutes(ms: number) {
    return (ms / 1000) / 60;
}
