import { useEffect, useState } from "react";

import { Trello } from "../types/trello";
import { useProvidedTrello } from "./useProvidedTrello";

export function useMember(t: Trello.PowerUp.IFrame, ...fields: ["all"] | Trello.PowerUp.MemberFields[]) {
    const state = useState<Trello.PowerUp.Member>();
    const [member, setMember] = state;

    useEffect(
        () => {
            const actualFields = fields !== undefined && fields.length > 0
                ? fields
                : ["id"] as Trello.PowerUp.MemberFields[];
            t.member(...actualFields).then(res => setMember(res));
        },
        [t],
    );

    return member;
}

export function useProvidedMember(...fields: ["all"] | Trello.PowerUp.MemberFields[]) {
    const t = useProvidedTrello();
    return useMember(t, ...fields);
}
