import { Trello } from "../types/trello";
import { useInit } from "../jk/hooks/useInit";
import { useProvidedTrello } from "./useProvidedTrello";

export type TrelloRenderOptions = {
    refresh: () => void;
    callImmediately?: boolean;
}

export function useTrelloRenderWithOptions(t: Trello.PowerUp.IFrame, options: TrelloRenderOptions) {
    useInit(() => {
        if (options.callImmediately)
            options.refresh();
        t.render(options.refresh);
    });
}

export function useTrelloRender(t: Trello.PowerUp.IFrame, refresh: () => void) {
    return useTrelloRenderWithOptions(t, {
        refresh,
        callImmediately: true,
    });
}

export function useProvidedTrelloRender(refresh: () => void) {
    const t = useProvidedTrello();
    return useTrelloRender(t, refresh);
}
