import { useMemo, useState } from "react";
import { useQuery, useQueryClient } from "react-query";

import { CurrentWork, getCurrentWork, setCurrentWork } from "../api/estimateApi";
import { Trello } from "../types/trello";
import { minutesToMs } from "../utils/timeParsing";
import { useInit } from "../jk/hooks/useInit";
import { useProvidedTrello } from "./useProvidedTrello";

const queryKey = "current_work";

export function useCurrentWorkQuery(t: Trello.PowerUp.IFrame) {
    // N.B. la query sembra che non venga cachata, penso che il motivo sia che
    // ogni volta che viene aperta una scheda viene avviata una nuova
    // istanza di react
    return useQuery(
        queryKey,
        () => getCurrentWork(t),
        {
            staleTime: minutesToMs(2),
            cacheTime: Infinity,
            refetchOnMount: false,
            refetchOnReconnect: false,
            refetchOnWindowFocus: false,
        },
    );
}

export function useCurrentWork() {
    const t = useProvidedTrello();
    const queryClient = useQueryClient();
    const query = useCurrentWorkQuery(t);
    const [isSaving, setIsSaving] = useState(false);

    // console.log("query stale: " + query.isStale);

    useInit(() => {
        t.render(() => {
            // console.log("rendering so refetch");
            query.refetch();
        });
    });

    const set = useMemo(
        () => async (updated: CurrentWork | null) => {
            setIsSaving(true);
            await setCurrentWork(t, updated);
            // console.log("setting query data");
            queryClient.setQueryData(queryKey, updated);
            setIsSaving(false);
        },
        [t, queryClient],
    );

    return useMemo(
        () => ({
            value: query.data,
            isSaving,
            set,
        }),
        [query.data, isSaving, set],
    );
}
