import { Trello } from "../types/trello";
import { editCurrentWorkSession, getWorkSessions, setWorkSessions, WorkSession } from "../api/estimateApi";
import { formatMinutesWithOptions, hoursToMs, msToMinutes } from "./timeParsing";
import { getElapsedMs } from "./dateMath";

type Options = {
    hoursPerDay: number,
    mouseEvent: React.MouseEvent<HTMLButtonElement, MouseEvent>,
}

type AskOptions = Options & {
    action?: () => Promise<void>,
}

type EditOptions = Options & {
    action: (session: WorkSession) => Promise<WorkSession>,
}

export async function askToCloseOldSession(t: Trello.PowerUp.IFrame, options: AskOptions): Promise<void> {
    const sessions = await getWorkSessions(t);
    const current = sessions[sessions.length - 1];

    if (current === undefined || current.endDate !== undefined)
        return options.action?.();

    const sessionAgeMs = getElapsedMs(current.startDate);

    if (sessionAgeMs < hoursToMs(options.hoursPerDay * 2))
        return options.action?.();

    const formattedAge = formatMinutesWithOptions(msToMinutes(sessionAgeMs), {
        hoursPerDay: 24,
    });

    return new Promise((resolve, reject) => {
        t.popup({
            type: "confirm",
            title: "Close old session?",
            message: `The current session looks old (${formattedAge}), do you want to close it and open a new one to save this work?`,
            confirmText: "Yes",
            onConfirm: async tp => {
                current.endDate = new Date();
                await setWorkSessions(t, sessions);
                resolve(options.action?.());
                await tp.closePopup();
            },
            cancelText: "No",
            onCancel: tp => {
                resolve(options.action?.());
                return tp.closePopup();
            },
            mouseEvent: options.mouseEvent.nativeEvent,
        });
    });
}

export function closeIfOldAndEditCurrentWorkSession(t: Trello.PowerUp.IFrame, options: EditOptions) {
    return askToCloseOldSession(t, {
        action: () => editCurrentWorkSession(t, options.action),
        hoursPerDay: options.hoursPerDay,
        mouseEvent: options.mouseEvent,
    });
}
