import { Trello } from "../types/trello";

export type UserMinutes = {
    [key: string]: number,
}

export type Estimate = {
    estimateMinutes: number,
    workMinutes: UserMinutes,
}

export type SummaryEstimate = {
    estimateMinutes: number,
    workMinutes: number,
    remainingMinutes: number,
}

export type CurrentWork = {
    cardId: string,
    startDate: Date,
}

export type WorkSession = {
    startDate: Date,
    endDate?: Date,
    workMinutes: number,
}

export async function getMyWorkMinutes(estimate: Estimate, t: Trello.PowerUp.IFrame): Promise<number> {
    const me = await t.member("id");
    return estimate.workMinutes[me.id];
}

export function getTotalWorkMinutes(estimate: Estimate) {
    let sum = 0;
    for (const workerId in estimate.workMinutes)
        sum += estimate.workMinutes[workerId];
    return sum;
}

export function getRemainingWorkMinutes(estimate: Estimate, dueComplete: boolean, totalWorkMinutes?: number) {
    if (dueComplete)
        return 0;

    if (totalWorkMinutes === undefined)
        totalWorkMinutes = getTotalWorkMinutes(estimate);

    return estimate.estimateMinutes - totalWorkMinutes;
}

export function* enumerateWork(estimate: Estimate) {
    for (const memberId in estimate.workMinutes) {
        yield {
            memberId,
            minutes: estimate.workMinutes[memberId],
        };
    }
}

export async function getCardEstimate(t: Trello.PowerUp.IFrame, cardId?: string): Promise<Estimate> {
    const estimate = JSON.parse(await t.get(cardId ?? "card", "shared", "estimate", "null")) as Estimate;
    if (estimate === null) {
        return {
            estimateMinutes: 0,
            workMinutes: {},
        };
    }
    return estimate;
}

export async function setCardEstimate(t: Trello.PowerUp.IFrame, estimate: Estimate, cardId?: string) {
    await t.set(cardId ?? "card", "shared", "estimate", JSON.stringify(estimate));
    return estimate;
}

export async function getAllCardsEstimates(t: Trello.PowerUp.IFrame, ...fields: (keyof Trello.PowerUp.Card)[]) {
    if (fields === undefined || fields.length === 0)
        fields = ["id"];

    const cards: Trello.PowerUp.Card[] = await t.cards(...fields);
    return await Promise.all(cards.map(card =>
        getCardEstimate(t, card.id).then(estimate => ({
            ...estimate,
            card: card,
        }))));
}

export async function getBoardEstimates(t: Trello.PowerUp.IFrame) {
    const estimates = await getAllCardsEstimates(t, "id", "dueComplete");

    const summary: SummaryEstimate = {
        estimateMinutes: 0,
        workMinutes: 0,
        remainingMinutes: 0,
    };

    for (const est of estimates) {
        summary.estimateMinutes += est.estimateMinutes;
        summary.workMinutes += getTotalWorkMinutes(est);

        const remaining = getRemainingWorkMinutes(est, est.card.dueComplete);
        summary.remainingMinutes += remaining > 0 ? remaining : 0;
    }

    return summary;
}

export async function getCurrentWork(t: Trello.PowerUp.IFrame) {
    // console.log("getting current work");

    const currentWork = JSON.parse(await t.get("board", "private", "work", "null")) as CurrentWork | null;

    if (currentWork !== null)
        // js parses dates as strings so i have to convert it here
        currentWork.startDate = new Date(currentWork.startDate);

    return currentWork;
}

export async function setCurrentWork(t: Trello.PowerUp.IFrame, currentWork: CurrentWork | null) {
    // console.log("setting current work");
    await t.set("board", "private", "work", JSON.stringify(currentWork ?? null));
}

export async function getWorkSessions(t: Trello.PowerUp.IFrame) {
    const sessions = JSON.parse(await t.get("board", "private", "work_sessions", "[]")) as WorkSession[];

    for (const s of sessions) {
        s.startDate = new Date(s.startDate);
        if (s.endDate !== undefined)
            s.endDate = new Date(s.endDate);
    }

    return sessions;
}

export async function setWorkSessions(t: Trello.PowerUp.IFrame, sessions: WorkSession[]) {
    await t.set("board", "private", "work_sessions", JSON.stringify(sessions));
}

const maxWorkSessions = 5;

function addWorkSession(sessions: WorkSession[]) {
    if (sessions.length >= maxWorkSessions)
        sessions.shift();

    const added: WorkSession = {
        startDate: new Date(),
        workMinutes: 0,
    };

    sessions.push(added);

    return added;
}

export async function editCurrentWorkSession(t: Trello.PowerUp.IFrame, action: (session: WorkSession) => Promise<WorkSession>) {
    const sessions = await getWorkSessions(t);
    let current = sessions[sessions.length - 1];

    if (current === undefined || current.endDate !== undefined)
        current = addWorkSession(sessions);

    sessions[sessions.length - 1] = await action(current);

    await setWorkSessions(t, sessions);
}
