import { useEffect, useState } from "react";
import { getElapsedMs } from "../utils/dateMath";

export function useElapsedMinutes(startDate: Date | undefined) {
    return useElapsedMs(startDate) / 1000 / 60;
}

export function useElapsedSeconds(startDate: Date | undefined) {
    return useElapsedMs(startDate) / 1000;
}

export function useElapsedMs(startDate: Date | undefined) {
    const [ms, setMs] = useState(0);

    useEffect(
        () => {
            if (startDate === undefined) {
                setMs(0);
                return;
            }

            // TODO: per rendere piÃ¹ efficente dopo che gli ms passati
            // sono >= 1 minuto potrei passare a un set interval della
            // durata di un minuto

            let animationFrameId: number;
            let canceled = false;

            function updateSeconds() {
                setMs(getElapsedMs(startDate!));

                if (!canceled)
                    animationFrameId = requestAnimationFrame(updateSeconds);
            }

            animationFrameId = requestAnimationFrame(updateSeconds);

            return () => {
                canceled = true;
                cancelAnimationFrame(animationFrameId);
            };
        },
        [startDate],
    );

    return ms;
}
