import React from "react";

import { useCurrentWork } from "../hooks/useCurrentWork";
import { useCardById, useProvidedCard } from "../hooks/useCard";
import { getCardEstimate, setCardEstimate } from "../api/estimateApi";
import { useElapsedMinutes } from "../hooks/useElapsed";
import { formatMinutesWithOptions, hoursPerDay } from "../utils/timeParsing";
import { truncateString } from "../utils/truncate";
import { useProvidedTrello } from "../hooks/useProvidedTrello";
import { useMember } from "../hooks/useMember";
import { addMinutesToDate, getElapsedSeconds } from "../utils/dateMath";
import { askToCloseOldSession, closeIfOldAndEditCurrentWorkSession } from "../utils/workSessionsUtils";

type Props = {
    onEstimateChanged?: () => void;
}

export function WorkButton(props: Props) {
    const t = useProvidedTrello();
    const currentWork = useCurrentWork();
    const elapsedWorkMinutes = useElapsedMinutes(currentWork.value?.startDate);
    const providedCard = useProvidedCard("id");
    const workCard = useCardById(t, currentWork.value?.cardId, "id", "name");
    const member = useMember(t, "id");

    const isWorking = currentWork.value?.cardId !== undefined && !currentWork.isSaving;

    return (providedCard || isWorking
        ? <button
            className={`work-button ${!isWorking ? "mod-primary" : "mod-danger"}`}
            onClick={async e => {
                if (member === undefined)
                    return;

                if (!isWorking && providedCard !== undefined) {
                    currentWork.set({
                        cardId: providedCard.id,
                        startDate: new Date(),
                    });

                    askToCloseOldSession(t, {
                        hoursPerDay,
                        mouseEvent: e,
                    });
                }
                else if (isWorking) {
                    const seconds = Math.round(getElapsedSeconds(currentWork.value!.startDate));
                    const minutes = seconds / 60;

                    currentWork.set(null);

                    const estimate = await getCardEstimate(t, currentWork.value!.cardId);
                    await setCardEstimate(
                        t,
                        {
                            ...estimate,
                            workMinutes: {
                                ...estimate.workMinutes,
                                [member.id]: (estimate.workMinutes[member.id] ?? 0) + minutes,
                            },
                        },
                        currentWork.value!.cardId,
                    );

                    await closeIfOldAndEditCurrentWorkSession(t, {
                        action: async session => {
                            if (session.workMinutes === 0)
                                session.startDate = addMinutesToDate(session.startDate, -minutes);
                            session.workMinutes += minutes;
                            return session;
                        },
                        hoursPerDay,
                        mouseEvent: e,
                    });

                    props.onEstimateChanged?.();
                }
            }}
        >
            {currentWork.isSaving && "..."}
            {(!isWorking && !currentWork.isSaving) ? "Start work" : "End work"}
            {isWorking && " " + formatMinutesWithOptions(elapsedWorkMinutes, { zero: "" })}
            {isWorking && providedCard?.id !== currentWork.value?.cardId
                && ` (on${providedCard ? " other" : ""} card${workCard === undefined ? "" : `: "${truncateString(workCard.name, 8)}"`})`
            }
        </button>
        : <p>
            No work is being time-tracked.
        </p>
    );
}
